<template>
    <div class="d-flex">
        <v-icon size="18">
            {{ `mdi-currency-${currency == 'SAR' ? 'rial' :currency.toLowerCase()}` }}
        </v-icon>
        <small class="align-self-center">({{currency}})</small>
    </div>
</template>
<script>
export default {
    name:'TableFieldUser',
    props: {
        item: {
            type: Object,
        },
        header: {
            type: Object
        }
    },
    computed: {
        currency () {
            return this.$options.filters.dynamic([this.item, this.header.value], this.header.format, this.getFormatArgs(this.header.formatArgs, this.item) || [])
        }
    } 
}
</script>

